"}},[_vm._v("De acuerdo con los lineamientos del Ministerio de Salud y Protección Social, la ruta de atención que debe funcionar a nivel intersectorial debe construirse localmente, involucrando a todas las entidades que deban participar, teniendo en cuenta la corresponsabilidad entre salud, protección y justicia, tomando como punto de referencia la situación y determinantes sociales de la salud en cada territorio. Con el propósito de que estas rutas de atención integral construidas por territorios se fortalezcan cada día, el Ministerio de Salud y Protección Social realiza acciones continuas para el fortalecimiento de las instituciones y el apoyo técnico a los departamentos, distritos y municipios que hagan parte de la priorización en el marco de la construcción de rutas intersectoriales para la atención integral de las víctimas de violencias de género, con énfasis en las violencias sexuales.")])]),_c('div',{staticClass:"col-md-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/12.png"),"data-aos":"fade-left"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bloque-texto-a bloque_3_6 color-acento-contenido p-4 p-md-5 mt-5 col-md-10 m-auto"},[_c('div',{staticClass:"row m-0 align-items-center justify-content-between"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0"}),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bloque-texto-a__texto p-5"},[_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("La mujer víctima de cualquier tipo de violencia basada en género debe, como primera medida, solicitar a la EPS la atención integral e inmediata, sin incurrir en ningún costo. Tener presente que la Circular 00016 de 2014 entrega las condiciones para la exención, totalidad de los grupos exentos de copagos y cuotas moderadoras, donde “todas las mujeres víctimas de violencia física o sexual certificadas por las autoridades competentes” están dentro del grupo de población exenta del pago de cuotas moderadoras y copagos. La EPS no podrá presentar una negativa a cumplir con esta obligación, que está inmersa en el plan de beneficios en salud; de no realizar la atención, inmediatamente se convierte en una falta al derecho de la salud.")])])])])])}]

